body{
  margin:0px;
}
footer {
  margin-top: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 210px;
  gap: 30px;
  color: rgb(255, 255, 255);
  fill: rgb(255, 255, 255);
  background-color: rgb(0, 0, 0);
  font-size: 24px;
  font-weight: 500;
  text-align: center;




  svg{
    width: 122px;
    margin-top: 4rem;
  }


}
@media (max-width: 768px) {
  svg{
    margin-top: 1rem;
  }

  p{
    font-size: 12px;
  }
}

